import React, { useState, useEffect } from 'react'
import { useHistory, Link } from 'react-router-dom'
import { Typography, Button, Result, Table, Row, Col, Divider, Input, Radio, Form, Space } from 'antd'
import { getCatalogos } from '../../utils/Utils'
import toast from 'react-hot-toast'

import UsuarioService from '../../service/UsuarioService'
const usuarioService = new UsuarioService()

const Usuarios = () => {

  const navigate = useHistory()
  const [load, setLoad] = useState(true)
  const [isLoading, setIsLoading] = useState(false)
  const [usuarios, setUsuarios] = useState([])
  const [error, setError] = useState(null)

  const [documento, setDocumento] = useState(null)
  const [email, setEmail] = useState(null)
  const [nombres, setNombres] = useState(null)
  const [estado, setEstado] = useState(null)

  const tableProps = {
    bordered: false,
    loading: false,
    title: '',
    showHeader: true,
    size: 'medium',
    pagination: {
      position: ['none', 'bottomRight']
    }
  };

  const columnas = [
    {
      title: '',
      dataIndex: 'usuId',
      render: (_) => <img alt='' src='./assets/user.png' width={32} height={32} />
    },
    {
      title: 'Name',
      dataIndex: 'nombres',
      render: (_, r) => `${r.nombres} ${r.apellidos}`
    },
    {
      title: 'ID Number',
      dataIndex: 'documento'
    },
    {
      title: 'Email',
      dataIndex: 'email',
      responsive: ['xl']
    },
    {
      title: 'Phone',
      dataIndex: 'celular',
      render: (_, r) => `${r.codigotel} ${r.celular}`,
      responsive: ['xl']
    },
    {
      title: 'Actions',
      render: (_, record) => <Button type='primary' href={`/#/users/${record.usuId}`}>View</Button>
    }
  ]

  const fetchData = async () => {
    setLoad(false)
    setError(null)
    setIsLoading(true)
    try {
      let filtros = {
      }
      if (documento) {
        filtros = { ...filtros, documento }
      }
      if (email) {
        filtros = { ...filtros, email }
      }
      if (nombres) {
        filtros = { ...filtros, nombres }
      }
      if (estado !== null) {
        filtros = { ...filtros, estadoId: estado }
      }
      const { data } = await usuarioService.getsFiltro({ navigate, filtros })
      if (data) {
        const usuarios = data.listObject[0]
        setUsuarios(usuarios)
      } else {
        setError("An error ocurred")
      }
    } catch (error) {
      const { response } = error
      toast.error(response.status === 409 ? response.data[0].messageText : "An error ocurred")
      // setError("An error ocurred")
    } finally {
      setIsLoading(false)
    }
  }

  useEffect(() => {
    if (load) fetchData()
  })

  if (error) {
    return (
      <Result
        status="warning"
        title={''}
        subTitle={''}
        extra={
          <>
            <h5>{error}</h5>
            <Link to={'/'}>Return to home</Link>
          </>
        }
      />
    )
  }

  return (
    <>
      <Row>
        <Col span={24}>
          <Row justify='start'>
            <Typography.Title
              level={4}
              style={{
                margin: 0,
              }}
            >
              Users Management
            </Typography.Title>
          </Row>
        </Col>
      </Row>
      <Divider></Divider>
      <Form
        layout={'vertical'}
      >
        <Row gutter={10}>
          <Col xs={24} sm={12} md={12} lg={6} xl={6}>
            <Form.Item label="ID Number">
              <Input label='ID Number' type='text' value={documento} onChange={(e) => setDocumento(e.target.value)} />
            </Form.Item>
          </Col>
          <Col xs={24} sm={12} md={12} lg={6} xl={6}>
            <Form.Item label="Name">
              <Input label='Name' type='text' value={nombres} onChange={(e) => setNombres(e.target.value)} />
            </Form.Item>
          </Col>
          <Col xs={24} sm={12} md={12} lg={6} xl={6}>
            <Form.Item label="Email">
              <Input label='Email' type='text' value={email} onChange={(e) => setEmail(e.target.value)} />
            </Form.Item>
          </Col>
          <Col xs={24} sm={12} md={12} lg={6} xl={6}>
            <Form.Item label="State">
              <Radio.Group onChange={(e) => setEstado(e.target.value)} value={estado}>
                {
                  getCatalogos().states.map(s => <Radio value={s.catId}>{s.catNombre}</Radio>)
                }
                {/* <Radio value={true}>Activo</Radio>
                <Radio value={false}>Inactivo</Radio> */}
              </Radio.Group>
            </Form.Item>
          </Col>
        </Row>
        <Row justify='end'>
          <Col>
            <Form.Item>
              <Space direction='horizontal'>
                <Button type='default' onClick={() => { navigate.push('/usernew') }} loading={isLoading} disabled={isLoading}>New User</Button>
                <Button type='primary' onClick={() => fetchData()} loading={isLoading} disabled={isLoading}>Refresh</Button>
              </Space>
            </Form.Item>
          </Col>
        </Row>
      </Form>
      <Table
        {...tableProps}
        rowKey='usuId'
        columns={columnas}
        dataSource={usuarios}
        loading={isLoading}
      />
    </>
  )
}

export default Usuarios